import AbstractDomain from './AbstractDomain'
import Project from './Project'
import Tag from './Tag'
import {JsonProperty} from 'jackson-js'
import Resource from '../decorators/Resource'

@Resource.Name('projectTags')
export default class ProjectTag extends AbstractDomain<ProjectTag> {
    @JsonProperty() public tagId: string
    @JsonProperty() public projectId: string
    @JsonProperty() public capacity?: number
    protected _project?: Project
    protected _tag?: Tag

    @JsonProperty()
    get tag(): Tag {
        return this._tag
    }

    set tag(value: Tag) {
        this._tag = value
    }

    @JsonProperty()
    get project(): Project {
        return this._project
    }

    set project(value: Project) {
        this._project = value
    }
}