import React, {useEffect} from 'react'
import useStore from '../hooks/useStore'
import Card from '../ui/Card'
import {Heading, HStack} from '@chakra-ui/react'
import {useRouteMatch} from 'react-router-dom'
import EditButton from '../components/EditButton'
import DisplayForRole, {EDIT_ROLES} from '../components/DisplayForRole'
import ProjectFormDrawer from '../components/ProjectFormDrawer'

interface ProjectOverviewProps {
}

const ProjectOverview: React.FC<ProjectOverviewProps> = () => {
  const match = useRouteMatch<{id: string}>()
  const {projectStore} = useStore()
  const id = match.params.id
  const project = projectStore.getById(id)

  useEffect(() => {
    projectStore.loadOne(id)
  }, [id])

  if (!project) {
    return null
  }

  return (
      <Card>
        <DisplayForRole roles={EDIT_ROLES}>
          <ProjectFormDrawer name={project.type} type={project.type} />
        </DisplayForRole>
        <HStack justifyContent="space-between">
          <Heading>{project.name}</Heading>
          <EditButton size="sm" to={`?${project.type}=${project.id}`}/>
        </HStack>
      </Card>
  )
}

export default ProjectOverview