import React from 'react'
import {Box, BoxProps} from '@chakra-ui/react'

export interface CardProps extends Partial<Pick<BoxProps, 'p'>> {
}

const Card: React.FC<CardProps> = ({children, ...props}) => {
  return (
      <Box bg="white" borderRadius="md" boxShadow="lg" p={4} overflow="hidden" {...props}>
        {children}
      </Box>
  )
}

export default Card