import React from 'react'
import {Input, Select, Stack} from '@chakra-ui/react'
import FormField from './FormField'
import FormImageField from './FormImageField'
import Person, {Role} from '../domain/Person'
import useStore from '../hooks/useStore'
import {observer} from 'mobx-react'
import DrawerForm from './DrawerForm'
import useQueryValue from '../hooks/useQueryValue'
import PersonTagsField from './PersonTagsField'
import ProjectTag from '../domain/ProjectTag'
import {parse} from '../util/json'
import PersonSkill from '../domain/PersonSkill'

interface PersonFormDrawerProps {
  simple?: boolean
}

const PersonFormDrawer: React.FC<PersonFormDrawerProps> = ({ simple }) => {
  const name = 'person'
  const {personStore, organizationStore, personSkillStore} = useStore()
  const personId = useQueryValue(name)

  const handleSubmit = async ({tags, ...values}: any, personId: string) => {
    const person = parse(values, Person)
    person.organizationId = organizationStore.currentOrganizationId

    if (personId) {
      person.id = personId
      await personStore.update(person)
    } else {
      await personStore.create(person)
    }


    console.log(tags)
    await personSkillStore.syncPersonSkills(person.id, tags.map((tag: any) => {
      const personSkill = new PersonSkill()
      personSkill.tagId = tag.value

      return personSkill
    }))
  }

  const person = {
    role: Role.USER,
    ...personId ? personStore.getById(personId) : {}
  }

  return (
      <DrawerForm
          name={name}
          onSubmit={handleSubmit}
          header={<>{personId ? 'Edit' : 'Add'} member</>}
          defaultValues={person}
      >
        <Stack spacing={4}>
          <FormField name="name" label="Name" required>
            <Input autoFocus={!personId} />
          </FormField>

          {!simple && (
              <FormField name="user.email" label="Email">
                <Input type="email" />
              </FormField>
          )}

          <FormField name="position" label="Position">
            <Input />
          </FormField>

          {!simple && (
              <FormField name="role" label="Role">
                <Select>
                  <option value={Role.ADMIN}>Admin</option>
                  <option value={Role.MANAGER}>Manager</option>
                  <option value={Role.USER}>User</option>
                </Select>
              </FormField>
          )}

          <PersonTagsField personId={personId} />

          <FormImageField label="Image" name="image" />
        </Stack>
      </DrawerForm>
  )
}

export default observer(PersonFormDrawer)