import React, {useEffect} from 'react'
import {useFieldArray, useFormContext} from 'react-hook-form'
import FormField from './FormField'
import useStore from '../hooks/useStore'
import {observer} from 'mobx-react'
import {TagType} from '../domain/Tag'
import ProjectTag from '../domain/ProjectTag'
import {Grid, Heading, HStack, IconButton, Input, Menu, MenuButton, MenuItem, MenuList, Stack,} from '@chakra-ui/react'
import {FaPlus} from 'react-icons/all'
import {FaTrash} from 'react-icons/fa'
import {parse} from '../util/json'

export interface ProjectPositionsFieldProps {
  projectId?: string
}

const ProjectPositionsField: React.FC<ProjectPositionsFieldProps> = ({projectId}) => {
  const name = 'positions'
  const {tagStore, projectTagStore} = useStore()
  const {control, watch, setValue} = useFormContext()
  const {fields, append, remove} = useFieldArray<ProjectTag>({
    control,
    name
  })

  useEffect(() => {
    tagStore.loadAll()
  }, [tagStore])

  useEffect(() => {
    if (projectId) {
      projectTagStore.loadByProject(projectId)
    }
  }, [projectTagStore, projectId])


  const projectPositions: ProjectTag[] = projectId ? projectTagStore.getItemsByProject(projectId)
      .filter(projectTag => projectTag.tag?.type === TagType.POSITION) : []

  useEffect(() => {
    if (projectPositions.length > 0) {
      setValue(name, projectPositions.map(({tagId, capacity = 1, tag}) => ({tagId, tag, capacity})))
    }
  }, [projectPositions.length])

  const values: ProjectTag[] = watch(name)
  const total = +values.reduce((a, b) => a + (+b.capacity || 1), 0).toFixed(2)

  const availablePositions = tagStore.positions.filter(tag => !fields.find(v => v.tagId === tag.id))

  if (!availablePositions.length) {
    return null
  }

  const filteredAvailablePositions = availablePositions.filter(tag => !fields.find(v => v.tagId === tag.id))

  return (
      <div>
        <Stack spacing={4}>
          <HStack justifyContent="space-between">
            <Heading size="sm">Positions ({total})</Heading>
            <Menu>
              <MenuButton
                  as={IconButton}
                  aria-label="Add"
                  size="sm"
                  icon={<FaPlus/>}
                  disabled={filteredAvailablePositions.length <= 0}
              />
              <MenuList>
                {filteredAvailablePositions.map(tag => (
                    <MenuItem key={tag.id} onClick={() => append({tagId: tag.id, capacity: 1, tag})}>
                      {tag.name}
                    </MenuItem>
                ))}
              </MenuList>
            </Menu>
          </HStack>
          {fields.map((item, index) => (
              <Grid key={item.id} alignItems="center"
                    templateColumns="1fr min-content min-content" gap={2}>
                <div>
                  {item.tag?.name}

                  <FormField name={`${name}[${index}].tagId`} defaultValue={item.tagId}>
                    <input type="hidden"/>
                  </FormField>
                </div>
                <FormField name={`${name}[${index}].capacity`} defaultValue={item.capacity}>
                  <Input placeholder="Capacity" type="number" width="20"/>
                </FormField>
                <IconButton
                    aria-label="Remove"
                    icon={<FaTrash/>}
                    onClick={() => remove(index)}
                />
              </Grid>
          ))}
        </Stack>
      </div>
  )
}

export default observer(ProjectPositionsField)