import React, {useEffect} from 'react'
import {Avatar, AvatarBadge, Tooltip} from '@chakra-ui/react'
import {Link} from 'react-router-dom'
import {FaRegBell} from 'react-icons/all'
import NotificationsDrawer from './NotificationsDrawer'
import useStore from '../hooks/useStore'
import {observer} from 'mobx-react'
import useInterval from '../hooks/useInterval'

export interface NotificationsHandleProps {
}

const NotificationsHandle: React.FC<NotificationsHandleProps> = () => {
  const {notificationStore} = useStore()
  const {currentPersonNotifications: notifications, currentPersonUnreadNotificationsCount: count} = notificationStore
  const label = count > 0 ? `${count} unread notification${count !== 1 ? 's' : ''}` : 'No unread notifications'

  useEffect(() => {
    notificationStore.loadByActivePerson()
  }, [])

  useInterval(() => {
    notificationStore.loadByActivePerson()
  }, 5000)

  return (
      <Tooltip label={label}>
        <div>
          <NotificationsDrawer notifications={notifications} />
          <Avatar
              size="sm"
              bg="white"
              as={Link}
              to={`?notifications`}
              aria-label="Notifications"
              icon={<FaRegBell fontSize="1.5rem"/>}
          >
            {count > 0 && <AvatarBadge borderColor="papayawhip" bg="tomato" boxSize="1em" />}
          </Avatar>
        </div>
      </Tooltip>
  )
}

export default observer(NotificationsHandle)