import {ObjectMapper} from 'jackson-js'
import {ClassType} from 'jackson-js/dist/@types'

const objectMapper = new ObjectMapper()

export function parse<T>(input: string | T | any, type?: ClassType<T>): T {
  if (typeof input !== 'string') {
    return parse<T>(stringify(input), type)
  }

  return objectMapper.parse<T>(input, type && {mainCreator: () => [type]})
}

export function stringify<T>(input: T): string {
  return objectMapper.stringify<T>(input)
}