import React from 'react'
import Project, {ProjectType} from '../domain/Project'
import {Button, HStack, SimpleGrid, Stack, VStack} from '@chakra-ui/react'
import ProjectCard from './ProjectCard'
import {Link} from 'react-router-dom'
import ProjectFormDrawer from './ProjectFormDrawer'
import useQueryValue from '../hooks/useQueryValue'
import DisplayForRole, {EDIT_ROLES} from './DisplayForRole'

export interface ProjectListProps {
  projects: Project[]
  type: ProjectType
}

const ProjectList: React.FC<ProjectListProps> = ({projects, type}) => {
  const highlightedProjectId = useQueryValue('highlighted-project-id')

  return (
      <div>
        <DisplayForRole roles={EDIT_ROLES}>
          <ProjectFormDrawer name={type} type={type} />
        </DisplayForRole>
        {projects.length ? (
            <Stack spacing={4}>
              <HStack justifyContent="flex-end">
                <DisplayForRole roles={EDIT_ROLES}>
                  <Button as={Link} to={`?${type}`}>
                    Add {type}
                  </Button>
                </DisplayForRole>
              </HStack>
              <SimpleGrid columns={3} spacing={4}>
                {projects.map(project => (
                    <ProjectCard highlighted={highlightedProjectId === project.id} key={project.id} project={project}/>
                ))}
              </SimpleGrid>
            </Stack>
        ) : (
            <VStack height="100%" justifyContent="center" spacing={8}>
              <div>No {type}s matching filters.</div>
              <DisplayForRole roles={EDIT_ROLES}>
                <Button colorScheme="purple" as={Link} to={`?${type}`}>
                  Create new {type}
                </Button>
              </DisplayForRole>
            </VStack>
        )}
      </div>
  )
}

export default ProjectList