import React, {useEffect} from 'react'
import useStore from '../hooks/useStore'
import {observer} from 'mobx-react'
import TagsGrid from './TagsGrid'
import {TagType} from '../domain/Tag'
import {FaUsers} from 'react-icons/fa'

export interface ProjectTagsProps {
  projectId: string
}

const ProjectTags: React.FC<ProjectTagsProps> = ({projectId}) => {
  const {projectTagStore} = useStore()

  useEffect(() => {
    projectTagStore.loadByProject(projectId)
  }, [projectId])

  const projectTags = projectTagStore.getItemsByProject(projectId)
  const positions = projectTags.filter(projectTag => projectTag.tag?.type === TagType.POSITION)
  const other = projectTags.filter(projectTag => projectTag.tag?.type !== TagType.POSITION)
  const tags: any[] = other.map(projectTag => projectTag.tag || {})

  const positionsCount = positions.reduce(((acc, current) => acc + (+current.capacity || 1)), 0)

  if (positionsCount > 0) {
    tags.splice(1, 0, {
      icon: <FaUsers />,
      name: positionsCount.toString(),
    })
  }

  return (
      <TagsGrid tags={tags} />
  )
}

export default observer(ProjectTags)