import AbstractRestfulLocalStorageService from './AbstractRestfulLocalStorageService'
import LocalStorageService from './LocalStorageService'
import ProjectTag from '../domain/ProjectTag'

export default class ProjectTagService extends AbstractRestfulLocalStorageService<ProjectTag> {
  public constructor(localStorageService: LocalStorageService<ProjectTag[]>) {
    super(ProjectTag, localStorageService)
  }

  public getAllByProject(projectId: string): Promise<ProjectTag[]> {
    return this.getMany({ projectId })
  }
}