import React from 'react'
import {IconButton, IconButtonProps} from '@chakra-ui/react'
import {FaPen} from 'react-icons/fa'
import DisplayForRole from './DisplayForRole'
import {Role} from '../domain/Person'
import Link from './Link'

export interface EditButtonProps extends Partial<Pick<IconButtonProps, 'size'>>{
  to: string
  scope?: Role[]
  label?: string
  icon?: React.ReactElement
}

const EditButton: React.FC<EditButtonProps> = ({to, scope, label, icon, ...iconButtonProps}) => {
  return (
      <DisplayForRole roles={scope}>
        <IconButton variant="ghost" as={Link} to={to} preserveQuery aria-label={label} icon={icon} {...iconButtonProps}/>
      </DisplayForRole>
  )
}

EditButton.defaultProps = {
  scope: [Role.MANAGER, Role.ADMIN],
  label: 'Edit',
  icon: <FaPen />
}

export default EditButton