import React from 'react'
import RouterDrawer from './RouterDrawer'
import {DrawerBody, DrawerHeader} from '../ui/Drawer'
import {observer} from 'mobx-react'
import Notification from '../domain/Notification'
import ReactMarkdown from 'react-markdown'
import {LinkBox, Stack, Heading, LinkOverlay, Text} from '@chakra-ui/react'
import {formatDistanceToNow} from 'date-fns'
import {Link} from 'react-router-dom'
import useStore from '../hooks/useStore'
import {formatDate} from '../util/date'

export interface NotificationsDrawerProps {
  notifications: Notification[]
}

const NotificationsDrawer: React.FC<NotificationsDrawerProps> = ({notifications}) => {
  const {notificationStore} = useStore()

  return (
      <RouterDrawer name="notifications">
        <DrawerHeader>Notifications</DrawerHeader>
        <DrawerBody>
          <Stack spacing={2}>
            {notifications?.map(notification => {
              const formattedDate = formatDate(notification.createdAt)

              return (
                  <div key={notification.id}>
                    <LinkBox opacity={notification.readAt ? .6 : 1} p="5" borderWidth="1px" rounded="md">
                      <Text fontSize="xs" color="gray.500" as="time" dateTime={formattedDate} title={formattedDate}>
                        {formatDistanceToNow(notification.createdAt, { addSuffix: true })}
                      </Text>
                      <Heading size="sm" my="2">
                        <LinkOverlay as={Link} to={notification.path} onClick={() => {
                          notificationStore.markAsRead(notification)
                        }}>
                          {notification.title}
                        </LinkOverlay>
                      </Heading>
                      <Text fontSize="xs">
                        <ReactMarkdown>{notification.content}</ReactMarkdown>
                      </Text>
                    </LinkBox>
                  </div>
              )
            })}
            {notifications.length <= 0 && 'No notifications'}
          </Stack>
        </DrawerBody>
      </RouterDrawer>
  )
}

export default observer(NotificationsDrawer)