import React from 'react'
import {Input, Stack} from '@chakra-ui/react'
import FormField from './FormField'
import useStore from '../hooks/useStore'
import DrawerForm from './DrawerForm'
import useQueryValue from '../hooks/useQueryValue'

interface PersonDeleteDrawerProps {
  name: string
}

const PersonDeleteDrawer: React.FC<PersonDeleteDrawerProps> = ({name}) => {
  const {personStore} = useStore()
  const personId = useQueryValue(name)

  if (!personId) {
    return null
  }

  const handleSubmit = async (data: any, personId: string) => {
    await personStore.delete(personId)
  }

  const person = personStore.getById(personId)

  if (!person) {
    return null
  }

  return (
      <DrawerForm name={name} onSubmit={handleSubmit} header="Delete member">
        <Stack spacing={4}>
          <FormField
              name="confirmation"
              label={`Type "${person.name}" to delete`}
              required
              validate={(value: string) => value === person.name || 'Invalid'}
          >
            <Input autoFocus/>
          </FormField>
        </Stack>
      </DrawerForm>
  )
}

export default PersonDeleteDrawer