import React from 'react'
import DisplayForRole, {EDIT_ROLES} from '../../components/DisplayForRole'
import PersonFormDrawer from '../../components/PersonFormDrawer'
import {Avatar, Heading, HStack, Stack, Text} from '@chakra-ui/react'
import EditButton from '../../components/EditButton'
import {observer} from 'mobx-react'
import Person from '../../domain/Person'

interface UserGeneralInformationProps {
  person: Person
}

const UserGeneralInformation: React.FC<UserGeneralInformationProps> = ({ person }) => {
  if (!person) {
    return null
  }

  return (
      <div>
        <DisplayForRole roles={EDIT_ROLES}>
          <PersonFormDrawer simple />
        </DisplayForRole>
        <HStack spacing={4}>
          <Avatar src={person.image} name={person.name}/>
          <Stack spacing={0}>
            <Heading size="md">{person.name}</Heading>
            <Text>{person.subTitle}</Text>
          </Stack>
          <EditButton to={`?person=${person.id}`}/>
        </HStack>
      </div>
  )
}

export default observer(UserGeneralInformation)