import AbstractDomain from './AbstractDomain'
import Person from './Person'
import Tag from './Tag'
import {JsonProperty} from 'jackson-js'
import Resource from '../decorators/Resource'

@Resource.Name('personSkills')
export default class PersonSkill extends AbstractDomain<PersonSkill> {
  @JsonProperty() public tagId: string
  @JsonProperty() public personId: string
  @JsonProperty() public level?: number
  protected _person?: Person
  protected _tag?: Tag

  @JsonProperty()
  get tag(): Tag {
    return this._tag
  }

  set tag(value: Tag) {
    this._tag = value
  }

  @JsonProperty()
  get person(): Person {
    return this._person
  }

  set person(value: Person) {
    this._person = value
  }
}