import React, {useEffect} from 'react'
import useStore from '../hooks/useStore'
import {TagType} from '../domain/Tag'
import FormField from './FormField'
import TagsSelect from './TagsSelect'
import {observer} from 'mobx-react'

export interface PersonTagsFieldProps {
  personId?: string
}

const PersonTagsField: React.FC<PersonTagsFieldProps> = ({personId}) => {
  const {tagStore, personSkillStore} = useStore()

  useEffect(() => {
    tagStore.loadAll()
  }, [tagStore])

  useEffect(() => {
    if (personId) {
      personSkillStore.loadByPerson(personId)
    }
  }, [personSkillStore, personId])

  const tags = tagStore.items.filter(t => [TagType.POSITION, TagType.TECHNOLOGY, TagType.LANGUAGE].includes(t.type))

  if (!tags.length) {
    return null
  }

  let defaultValue: null | { value: string, label: string }[] = null

  if (personId) {
    defaultValue = personSkillStore.getItemsByPerson(personId)
        .map(personSkill => ({
          value: personSkill.tagId,
          label: personSkill.tag?.name,
          tag: personSkill.tag
        }))
  }

  return (
      <FormField name="tags" label="Skills" defaultValue={defaultValue}>
        <TagsSelect tags={tags}/>
      </FormField>
  )
}

export default observer(PersonTagsField)