import AbstractDomain from './AbstractDomain'
import {JsonProperty} from 'jackson-js'
import Resource from '../decorators/Resource'

@Resource.Name('notifications')
export default class Notification extends AbstractDomain<Notification> {
  @JsonProperty() public createdAt: Date = new Date()
  @JsonProperty() public title: string
  @JsonProperty() public content: string
  @JsonProperty() public path: string
  @JsonProperty() public readAt?: Date
  @JsonProperty() public personId: string
}