import React, {useEffect} from 'react'
import Project, {ProjectType} from '../domain/Project'
import useStore from '../hooks/useStore'
import ProjectList from './ProjectList'

interface ConnectedProjectListProps {
  filters: Partial<Project>
}

const ConnectedProjectList: React.FC<ConnectedProjectListProps> = ({filters}) => {
  const {projectStore} = useStore()

  useEffect(() => {
    projectStore.loadByActiveOrganization()
  }, [])


  return (
      <ProjectList
          type={filters.type || ProjectType.PROJECT}
          projects={projectStore.getFilteredItems(filters)}
      />
  )
}

export default ConnectedProjectList