import React from 'react'
import {observer} from 'mobx-react'
import ConnectedProjectList from '../components/ConnectedProjectList'
import {ProjectState, ProjectType} from '../domain/Project'

const UpcomingTrainings = () => {
  return (
      <ConnectedProjectList filters={{type: ProjectType.TRAINING, state: ProjectState.UPCOMING}}/>
  )
}

export default observer(UpcomingTrainings)