import AbstractDomain from './AbstractDomain'
import {JsonProperty} from 'jackson-js'
import Resource from '../decorators/Resource'

@Resource.Name('organizations')
export default class Organization extends AbstractDomain<Organization> {
  @JsonProperty() public name: string
  @JsonProperty() public logo?: string
  @JsonProperty() public address: string
  @JsonProperty() public vatNumber: string
  @JsonProperty() public email: string
  @JsonProperty() public phone: string
  @JsonProperty() public registryCode: string
  @JsonProperty() public industry: string
  @JsonProperty() public status: string
}