import React from 'react'
import TagToIcon from './TagToIcon'
import {HStack, Text} from '@chakra-ui/react'
import {TagType} from '../domain/Tag'

export interface TagProps {
  name: string
  type?: TagType
  icon?: any
}

const TagWithIcon: React.FC<TagProps> = ({children, name, type, icon}) => {
  return (
      <HStack
          spacing={2}
          py={1}
      >
        <span style={{flexShrink: 0}}>{icon || <TagToIcon name={name} type={type} />}</span>
        <Text fontSize="xs">{children || name}</Text>
      </HStack>
  )
}

export default TagWithIcon