import React, {useEffect} from 'react'
import useStore from '../../hooks/useStore'
import {observer} from 'mobx-react'
import DataTable from '../../components/DataTable'
import Person from '../../domain/Person'
import RoleBadge from '../../components/RoleBadge'
import PersonUser from '../../components/PersonUser'
import {Heading, HStack, IconButton} from '@chakra-ui/react'
import PersonFormDrawer from '../../components/PersonFormDrawer'
import {Link} from 'react-router-dom'
import PersonDeleteDrawer from '../../components/PersonDeleteDrawer'
import EditButton from '../../components/EditButton'
import DeleteButton from '../../components/DeleteButton'
import DisplayForRole, {EDIT_ROLES} from '../../components/DisplayForRole'
import {FaUserPlus} from 'react-icons/all'

const Members: React.FC = () => {
  const {personStore} = useStore()

  useEffect(() => {
    personStore.loadCurrentOrganizationPersons()
  }, [personStore])

  return (
      <div>
        <DisplayForRole roles={EDIT_ROLES}>
          <PersonFormDrawer/>
          <PersonDeleteDrawer name="person.delete"/>
        </DisplayForRole>
        <HStack spacing={4}>
          <Heading size="md">
            List of users
          </Heading>
          <DisplayForRole roles={EDIT_ROLES}>
            <IconButton variant="ghost" as={Link} to="?person" aria-label="Add" icon={<FaUserPlus />} />
          </DisplayForRole>
        </HStack>
        <DataTable<Person>
            data={personStore.currentOrganizationPersons}
            columns={[
              {
                accessor: p => p.name,
                title: 'Name',
                render: (name, person) => {
                  return (
                      <PersonUser person={person}/>
                  )
                }
              },
              {
                accessor: p => p.user?.email,
                title: 'Email',
              },
              {
                accessor: p => p.humanReadableRole,
                title: 'Account',
                render: (value, item) => (
                    <RoleBadge person={item}/>
                )
              },
              {
                accessor: () => null,
                render: (value, item) => (
                    item.id === personStore.currentPerson.id ? (
                        null
                    ) : (
                        <HStack spacing={0} justifyContent="flex-end">
                          <EditButton to={`?person=${item.id}`}/>
                          <DeleteButton to={`?person.delete=${item.id}`}/>
                        </HStack>
                    )
                )
              },
            ]}
        />
      </div>
  )
}

export default observer(Members)