import React from 'react'
import Project from '../domain/Project'
import {Button, Divider, HStack, Image, ScaleFade, Stack, Text} from '@chakra-ui/react'
import {formatDate} from '../util/date'
import Card from '../ui/Card'
import Badge from '../ui/Badge'
import {Link} from 'react-router-dom'
import ProjectTags from './ProjectTags'
import EditButton from './EditButton'

export interface ProjectCardProps {
  project: Project
  highlighted?: boolean
}

const ProjectCard: React.FC<ProjectCardProps> = ({project, highlighted}) => {
  const detailViewUri = `/projects/view/${project.id}`

  const content = (
      <Card p={0}>
        <Stack spacing={2}>
          <Link to={detailViewUri}>
            <Image
                width="100%"
                height={200}
                src={project.image}
                alt={project.name}
                objectFit="cover"
            />
          </Link>

          <Text fontSize="lg" textAlign="center" px={4}>
            {project.name}
            {project.clientName && <Badge ml={2} colorScheme="purple" size="xs">{project.clientName}</Badge>}
          </Text>

          {(project.dateStart || project.dateEnd) && (
              <Text fontSize="xs" fontWeight="bold" textAlign="center" color="gray.500" px={4}>
                {formatDate(project.dateStart) || '...'} — {formatDate(project.dateEnd) || '...'}
              </Text>
          )}

          <Stack px={4} alignItems="center">
            <ProjectTags projectId={project.id}/>
          </Stack>

          <Stack p={4}>
            <Divider/>

            <HStack justifyContent="flex-end" spacing={2}>
              <EditButton size="sm" to={`?${project.type}=${project.id}`}/>
              <Button size="sm" as={Link} to={detailViewUri} colorScheme="purple">Open</Button>
            </HStack>
          </Stack>
        </Stack>
      </Card>
  )

  if (!highlighted) {
    return content
  }

  return (
      <ScaleFade in initialScale={0.3}>
        {content}
      </ScaleFade>
  )
}

export default ProjectCard