import RootService from '../services'
import RootStore from './index'
import AbstractResourceStore from './AbstractResourceStore'
import Tag, {TagType} from '../domain/Tag'
import TagService from '../services/TagService'
import {computed} from 'mobx'

export default class TagStore extends AbstractResourceStore<Tag> {
  private readonly tagService: TagService
  private readonly rootStore: RootStore

  public constructor(rootService: RootService, rootStore: RootStore) {
    super(rootService.tagService, rootStore.accountStore)
    this.tagService = rootService.tagService
    this.rootStore = rootStore
  }

  @computed
  public get tagsForProject() {
    return this.items.filter(item => item.type !== TagType.POSITION)
  }

  @computed
  public get positions() {
    return this.items.filter(item => item.type === TagType.POSITION)
  }

  @computed
  public get skills() {
    return this.items.filter(item => [TagType.LANGUAGE, TagType.TECHNOLOGY].includes(item.type))
  }
}