import Notification from '../domain/Notification'

export default class NotificationBuilder {
  public constructor(private readonly notification: Notification) {

  }

  public static init(): NotificationBuilder {
    return new NotificationBuilder(new Notification())
  }

  public build(): Notification {
    return this.notification
  }

  public title(value: string): NotificationBuilder {
    this.notification.title = value

    return this
  }

  public content(value: string): NotificationBuilder {
    this.notification.content = value

    return this
  }

  public path(value: string): NotificationBuilder {
    this.notification.path = value

    return this
  }

  public personId(value: string): NotificationBuilder {
    this.notification.personId = value

    return this
  }
}