import React from 'react'
import ActiveProjects from './views/ActiveProjects'
import {FaBookOpen, FaBuilding, FaCalendarDay, FaHistory, FaPlay, FaUser} from 'react-icons/fa'
import UpcomingProjects from './views/UpcomingProjects'
import EndedProjects from './views/EndedProjects'
import OrganizationProfile from './views/OrganizationProfile'
import ProjectOverview from './views/ProjectOverview'
import MyProfile from './views/MyProfile'
import ComingSoon from './views/ComingSoon'
import {FaHashtag} from 'react-icons/all'
import UpcomingTrainings from './views/UpcomingTrainings'

export enum Placement {
  USER_MENU = 'User menu',
  PROJECTS = 'Projects',
  MARKETPLACE = 'Marketplace',
  NONE = 'None'
}

export interface Route {
  path: string
  title: string
  icon?: React.ReactNode,
  component?: React.FC,
  placement: Placement
}

const routes: Route[] = [
  {
    path: '/projects/active',
    title: 'Active',
    component: ActiveProjects,
    icon: <FaPlay />,
    placement: Placement.PROJECTS,
  },
  {
    path: '/projects/scheduled',
    title: 'Scheduled',
    component: UpcomingProjects,
    icon: <FaCalendarDay />,
    placement: Placement.PROJECTS,
  },
  {
    path: '/projects/ended',
    title: 'History',
    component: EndedProjects,
    icon: <FaHistory />,
    placement: Placement.PROJECTS,
  },
  {
    path: '/projects/view/:id',
    title: 'View project',
    component: ProjectOverview,
    placement: Placement.NONE
  },
  {
    path: '/marketplace/requests',
    title: 'Requests',
    component: ComingSoon,
    icon: <FaHashtag />,
    placement: Placement.MARKETPLACE
  },
  {
    path: '/marketplace/trainings',
    title: 'Trainings',
    component: UpcomingTrainings,
    icon: <FaBookOpen />,
    placement: Placement.MARKETPLACE
  },
  {
    path: '/profile/organization',
    title: 'Organization profile',
    component: OrganizationProfile,
    icon: <FaBuilding />,
    placement: Placement.USER_MENU
  },
  {
    path: '/profile/me',
    title: 'My profile',
    component: MyProfile,
    icon: <FaUser />,
    placement: Placement.USER_MENU
  },
  {
    path: '/create-organization',
    title: 'Create organization',
    placement: Placement.USER_MENU
  },
  {
    path: '/logout',
    title: 'Sign out',
    placement: Placement.USER_MENU
  }
]

export default routes