import AbstractRestfulLocalStorageService from './AbstractRestfulLocalStorageService'
import Project from '../domain/Project'
import LocalStorageService from './LocalStorageService'
import ProjectService from './ProjectService'
import NotificationBuilder from '../builders/NotificationBuilder'
import NotificationService from './NotificationService'
import PersonService from './PersonService'

export default class LocalProjectService extends AbstractRestfulLocalStorageService<Project> implements ProjectService {
  public constructor(localStorageService: LocalStorageService<Project[]>, private readonly notificationService: NotificationService, private readonly personService: PersonService) {
    super(Project, localStorageService)
  }

  public getAllByOrganization(organizationId: string): Promise<Project[]> {
    return this.getMany({ organizationId })
  }

  public async create(item: Project): Promise<Project> {
    const result = await super.create(item)

    const persons = await this.personService.getAllByOrganization(item.organizationId)

    persons.forEach((p) => {
      this.notificationService.create(
          NotificationBuilder
              .init()
              .title('New project')
              .content(`New project *${item.name}* has been created. Go check it out!`)
              .path('/not-found')
              .personId(p.id)
              .build()
      )
    })

    return result
  }
}