import React from 'react'
import {Tabs as ChakraTabs, TabsProps as ChakraTabsProps, TabList, TabPanels, Tab as ChakraTab, TabPanel} from '@chakra-ui/react'

interface TabsProps extends Partial<Pick<ChakraTabsProps, 'defaultIndex' | 'index' | 'onChange'>> {

}

const Tabs: React.FC<TabsProps> = ({children, ...props}) => {
  return (
      <ChakraTabs colorScheme="purple" variant="unstyled" isLazy {...props}>
        <TabList>
          {React.Children.map(children, (child, index) => {
            if (!React.isValidElement(child)) {
              return
            }

            return (
                <ChakraTab
                    mr={4}
                    rounded="md"
                    border="1px"
                    borderColor="transparent"
                    fontWeight="500"
                    _selected={{borderWidth: 1, color: 'purple.300', borderColor: 'purple.300'}}
                    key={child.props.title}>
                  {child.props.title}
                </ChakraTab>
            )
          })}
        </TabList>
        <TabPanels>
          {React.Children.map(children, (child, index) => {
            if (!React.isValidElement(child)) {
              return
            }

            return (
                <TabPanel key={child.props.title}>
                  {child}
                </TabPanel>
            )
          })}
        </TabPanels>
      </ChakraTabs>
  )
}

export default Tabs