import React, {useEffect} from 'react'
import useStore from '../../hooks/useStore'
import {Box, Heading, HStack, SimpleGrid, Stack, Text} from '@chakra-ui/react'
import {observer} from 'mobx-react'
import PersonUser from '../../components/PersonUser'
import {TagType} from '../../domain/Tag'
import useQueryValue from '../../hooks/useQueryValue'
import TagTypeSelect from '../../components/TagTypeSelect'
import {useHistory} from 'react-router-dom'

const Skillset: React.FC = () => {
  const {personStore, personSkillStore, tagStore} = useStore()
  const type = useQueryValue('type') as TagType || TagType.POSITION
  const history = useHistory()

  useEffect(() => {
    tagStore.loadAll()
        .then(() => personStore.loadCurrentOrganizationPersons())
        .then(() => personSkillStore.loadAll())
  }, [personStore])

  return (
      <div>
        <HStack spacing={4} justifyContent="space-between">
          <Heading size="md">
            Company skillset
          </Heading>

          <TagTypeSelect
              value={type}
              onSelect={(selectedType) => history.push(`?type=${selectedType}`)}
          />
        </HStack>

        <SimpleGrid columns={3} spacing={4} mt={4}>
          {tagStore.getFilteredItems({type}).map(tag => {
            const persons = personStore.currentOrganizationPersons
                .filter(person => personSkillStore.getFilteredItems({
                  tagId: tag.id,
                  personId: person.id
                }).length > 0)

            return (
                <div key={tag.id}>
                  <Box borderWidth="1px" p={2} rounded="md">
                    <Text color="gray.500" fontSize="xs" fontWeight="500" casing="uppercase" mb={2}>{tag.name}</Text>
                    <Stack spacing={2}>
                      {persons.map(person => (
                              <PersonUser
                                  key={person.id}
                                  person={person}
                                  subTitle={person.position}
                              />
                          ))}
                    </Stack>
                    {persons.length === 0 && (
                        <Text>
                          -
                        </Text>
                    )}
                  </Box>
                </div>
            )
          })}
        </SimpleGrid>
      </div>
  )
}

export default observer(Skillset)