import AbstractRestfulLocalStorageService from './AbstractRestfulLocalStorageService'
import LocalStorageService from './LocalStorageService'
import Notification from '../domain/Notification'

export default class NotificationService extends AbstractRestfulLocalStorageService<Notification> {
  public constructor(localStorageService: LocalStorageService<Notification[]>) {
    super(Notification, localStorageService)
  }

  public getAllByPerson(personId: string): Promise<Notification[]> {
    return this.getMany({ personId })
  }
}