import AbstractRestfulLocalStorageService from './AbstractRestfulLocalStorageService'
import LocalStorageService from './LocalStorageService'
import PersonSkill from '../domain/PersonSkill'

export default class PersonSkillService extends AbstractRestfulLocalStorageService<PersonSkill> {
  public constructor(localStorageService: LocalStorageService<PersonSkill[]>) {
    super(PersonSkill, localStorageService)
  }

  public getAllByPerson(personId: string): Promise<PersonSkill[]> {
    return this.getMany({ personId })
  }
}