import React from 'react'
import Tag from '../domain/Tag'
import ReactSelect, {components, Props} from 'react-select'
import TagWithIcon from './TagWithIcon'
import {observer} from 'mobx-react'

const MultiValueLabel: React.FC<any> = (props) => {
  return (
      <components.MultiValueLabel {...props}>
        <TagWithIcon name={props.data.tag?.name} type={props.data.tag?.type}/>
      </components.MultiValueLabel>
  )
}

const Option: React.FC<any> = (props) => {
  return (
      <components.Option {...props}>
        <TagWithIcon name={props.data.tag?.name} type={props.data.tag?.type}/>
      </components.Option>
  )
}

interface TagsSelectProps extends Props<any, true, any> {
  tags: Tag[]
}

const TagsSelect: React.FC<TagsSelectProps> = ({tags, ...props}) => {
  return (
      <ReactSelect
          isMulti
          components={{Option, MultiValueLabel}}
          options={tags.map(tag => (
              {
                value: tag.id,
                label: tag.name,
                tag,
              }
          ))}
          {...props}
      />
  )
}

export default observer(TagsSelect)