import React from 'react'
import {Input, Stack, SimpleGrid} from '@chakra-ui/react'
import FormField from './FormField'
import useStore from '../hooks/useStore'
import {observer} from 'mobx-react'
import DrawerForm from './DrawerForm'
import useQueryValue from '../hooks/useQueryValue'
import Project, {ProjectType} from '../domain/Project'
import DateInput from '../ui/DateInput'
import {toDate} from '../util/date'
import FormImageField from './FormImageField'
import {parse} from '../util/json'
import ProjectTagsField from './ProjectTagsField'
import ProjectPositionsField from './ProjectPositionsField'
import ProjectTag from '../domain/ProjectTag'

interface ProjectFormDrawerProps {
  name: string
  type?: ProjectType
}

const ProjectFormDrawer: React.FC<ProjectFormDrawerProps> = ({name, type}) => {
  const {projectStore, organizationStore, projectTagStore} = useStore()
  const projectId = useQueryValue(name)

  let project: Project

  if (projectId) {
    project = projectStore.getByIdOrLoad(projectId)
  }

  if (projectId && !project) {
    return null
  }

  const handleSubmit = async ({dateStart, dateEnd, tags, positions, ...values}: any, projectId: string) => {
    values.dateStart = dateStart ? toDate(dateStart) : null
    values.dateEnd = dateEnd ? toDate(dateEnd) : null
    const projectTags: ProjectTag[] = []

    const project = parse({
      type,
      ...values
    }, Project)

    project.organizationId = organizationStore.currentOrganizationId

    if (projectId) {
      project.id = projectId
      await projectStore.update(project)
    } else {
      await projectStore.create(project)
    }

    if (!project) {
      throw new Error('Something went wrong')
    }

    if (positions) {
      projectTags.push(...positions)
    }

    if (tags) {
      projectTags.push(...tags.map((tag: any) => {
        const projectTag = new ProjectTag()
        projectTag.tagId = tag.value

        return projectTag
      }))
    }

    await projectTagStore.syncProjectTags(project.id, projectTags)
  }

  return (
      <DrawerForm
          size="xl"
          name={name}
          onSubmit={handleSubmit}
          header={<>{projectId ? 'Edit' : 'Add'} {type}</>}
          defaultValues={project || {}}
      >
        <SimpleGrid columns={2} spacing={4}>
          <Stack spacing={4}>
            <FormField
                name="name"
                required
                label="Name"
            >
              <Input
                  size="lg"
                  autoFocus
              />
            </FormField>

            <FormField
                name="clientName"
                label={type === ProjectType.TRAINING ? 'Provider' : 'Client'}
            >
              <Input/>
            </FormField>

            <ProjectTagsField projectId={projectId}/>

            <FormField
                name="dateStart"
                label="Starts at"
            >
              <DateInput/>
            </FormField>

            <FormField
                name="dateEnd"
                label="Ends at"
            >
              <DateInput/>
            </FormField>

            <FormImageField
                name="image"
                label="Cover image"
            />

          </Stack>
          <Stack>
            <ProjectPositionsField projectId={projectId}/>
          </Stack>
        </SimpleGrid>
      </DrawerForm>
  )
}

ProjectFormDrawer.defaultProps = {
  type: ProjectType.PROJECT
}

export default observer(ProjectFormDrawer)