import React from 'react'
import {FaAndroid, FaAngular, FaBootstrap, FaJava, FaPhp, FaReact, FaHashtag} from 'react-icons/all'

interface TextToIconProps {
  name: string
  fallback?: React.ReactNode
}

const iconByText: { [key: string]: React.ReactNode } = {
  android: <FaAndroid color="#85C808"/>,
  angular: <FaAngular color="#DD0031"/>,
  bootstrap: <FaBootstrap color="#563D7C"/>,
  react: <FaReact color="#61DBFB"/>,
  java: <FaJava color="red"/>,
  php: <FaPhp color="#474A8A"/>,
  estonian: '🇪🇪',
  english: '🇬🇧',
}

const TextToIcon: React.FC<TextToIconProps> = ({name, fallback}) => {
  const lowercasedName: string = name ? name.toLocaleLowerCase() : ''

  return (
      <>
        {iconByText[lowercasedName] || fallback}
      </>
  )
}

TextToIcon.defaultProps = {
  fallback: <FaHashtag/>
}

export default TextToIcon