import React, {useEffect} from 'react'
import useStore from '../hooks/useStore'
import Layout from '../ui/Layout'
import {Placement, Route as NavRoute} from '../routes'
import NavItemGroup from '../ui/NavItemGroup'
import NavItem from '../ui/NavItem'
import {Link, Route, Switch} from 'react-router-dom'
import {observer} from 'mobx-react'
import Menu from '../ui/Menu'
import PersonUser from './PersonUser'
import NotificationsHandle from './NotificationsHandle'
import {HStack, Text} from '@chakra-ui/react'

interface ConnectedLayoutProps {
  routes: NavRoute[]
}

const ConnectedLayout: React.FC<ConnectedLayoutProps> = ({routes}) => {
  const {personStore} = useStore()
  const {currentUserNonActivePersons, currentPerson} = personStore

  useEffect(() => {
    personStore.loadUserRelated()
  }, [])

  return (
      <Layout
          navigation={(
              <div>
                {[Placement.PROJECTS, Placement.MARKETPLACE].map(placement => {
                  const filteredRoutes = routes.filter(route => route.placement === placement)

                  if (!filteredRoutes.length) {
                    return null
                  }

                  return (
                      <NavItemGroup key={placement} title={placement}>
                        {filteredRoutes.map(route => {
                          return (
                              <NavItem
                                  key={route.path}
                                  to={route.path}
                                  title={route.title}
                                  icon={route.icon}
                              />
                          )
                        })}
                      </NavItemGroup>
                  )
                })}
              </div>
          )}
          topRight={(
              <>
                <NotificationsHandle />
                <Menu handle={<PersonUser align="right" person={currentPerson}/>}>
                  {currentUserNonActivePersons.map((person) => (
                      <Link key={person.id} to={`/switch-person/${person.id}`}>
                        <PersonUser
                            align="right"
                            person={person}
                        />
                      </Link>
                  ))}
                  {routes.filter(route => route.placement === Placement.USER_MENU).map((route) => (
                      <HStack key={route.path} as={Link} to={route.path} spacing={2}>
                        <Text fontSize="sm" pr={2}>{route.title}</Text>
                        {route.icon}
                      </HStack>
                  ))}
                </Menu>
              </>
          )}
      >
        <Switch>
          {routes
              .filter(route => !!route.component)
              .map((route) => (
                  <Route key={route.path} path={route.path} component={route.component}/>
              ))
          }
        </Switch>
      </Layout>
  )
}

export default observer(ConnectedLayout)