import React from 'react'
import {observer} from 'mobx-react'
import ConnectedProjectList from '../components/ConnectedProjectList'
import {ProjectState, ProjectType} from '../domain/Project'

const UpcomingProjects = () => {
  return (
      <ConnectedProjectList filters={{type: ProjectType.PROJECT, state: ProjectState.ONGOING}}/>
  )
}

export default observer(UpcomingProjects)