import React from 'react'
import {TagType} from '../domain/Tag'
import TextToIcon from './TextToIcon'
import {FaCode, FaTag, FaUserTag, FaLanguage} from 'react-icons/all'
import {FaLocationArrow} from 'react-icons/fa'

interface TagToIconProps {
  name: string
  type: TagType
}

const iconByTagType: { [key in TagType]: React.ReactNode } = {
  [TagType.TECHNOLOGY]: <FaCode/>,
  [TagType.LOCATION]: <FaLocationArrow/>,
  [TagType.POSITION]: <FaUserTag/>,
  [TagType.PROJECT_TYPE]: <FaTag/>,
  [TagType.LANGUAGE]: <FaLanguage/>
}

const TagToIcon: React.FC<TagToIconProps> = ({name, type}) => {
  return (
      <TextToIcon
          name={name}
          fallback={(
              <>
                {iconByTagType[type] || TextToIcon.defaultProps.fallback}
              </>
          )}
      />
  )
}

export default TagToIcon