import config from '../config'
import LocalStorageService from './LocalStorageService'
import HttpClientService from './HttpClientService'
import TokenService from './TokenService'
import HttpClient from './HttpClient'
import PersonService from './PersonService'
import ProjectService from './ProjectService'
import LocalPersonService from './LocalPersonService'
import LocalProjectService from './LocalProjectService'
import OrganizationService from './OrganizationService'
import LocalOrganizationService from './LocalOrganizationService'
import AccountService from './AccountService'
import UserService from './UserService'
import LocalUserService from './LocalUserService'
import TagService from './TagService'
import ProjectTagService from './ProjectTagService'
import AttachmentUploadService from './AttachmentUploadService'
import NotificationService from './NotificationService'
import PersonSkillService from './PersonSkillService'

export default class RootService {
  public constructor(
      public readonly attachmentUploadService: AttachmentUploadService,
      public readonly accountService: AccountService,
      public readonly personService: PersonService,
      public readonly projectService: ProjectService,
      public readonly organizationService: OrganizationService,
      public readonly userService: UserService,
      public readonly tagService: TagService,
      public readonly projectTagService: ProjectTagService,
      public readonly notificationService: NotificationService,
      public readonly personSkillService: PersonSkillService
  ) {
  }

  private static getHttpClient(): HttpClient {
    return this.getHttpClientService().create(config.dataSource || undefined)
  }

  private static getLocalStorageService<T>() {
    return new LocalStorageService<T>()
  }

  private static getTokenService(): TokenService {
    return new TokenService(new LocalStorageService<string>())
  }

  private static getHttpClientService(): HttpClientService {
    return new HttpClientService(this.getTokenService())
  }

  private static getAttachmentUploadService(): AttachmentUploadService {
    return new AttachmentUploadService(this.getHttpClientService().create('https://mmm.planet.ee/filey', false, null))
  }

  public static initialize(): RootService {
    const localStorageService: LocalStorageService<any> = new LocalStorageService()
    const tokenService = this.getTokenService()
    const userService = new LocalUserService(localStorageService)
    const notificationService = new NotificationService(localStorageService)
    const personService = new LocalPersonService(localStorageService, tokenService, notificationService)

    return new RootService(
        this.getAttachmentUploadService(),
        new AccountService(this.getHttpClient(), tokenService, userService),
        personService,
        new LocalProjectService(localStorageService, notificationService, personService),
        new LocalOrganizationService(localStorageService),
        userService,
        new TagService(localStorageService),
        new ProjectTagService(localStorageService),
        notificationService,
        new PersonSkillService(localStorageService),
    )
  }
}