import React from 'react'
import {Button, Menu, MenuButton, MenuItem, MenuList} from '@chakra-ui/react'
import {FaChevronDown} from 'react-icons/fa'
import {TagType} from '../domain/Tag'
import TagWithIcon from './TagWithIcon'

export interface TagTypeSelectProps {
  value: TagType
  onSelect: (value: TagType) => void
}

const humanReadableTypes: { [key in TagType]: string } = {
  [TagType.POSITION]: 'Position',
  [TagType.LANGUAGE]: 'Language',
  [TagType.TECHNOLOGY]: 'Technology',
  [TagType.LOCATION]: 'Location',
  [TagType.PROJECT_TYPE]: 'Project',
}

const TagTypeSelect: React.FC<TagTypeSelectProps> = ({ value, onSelect }) => {
  return (
      <Menu>
        <MenuButton as={Button} variant="ghost" rightIcon={<FaChevronDown/>}>
          <TagWithIcon name={humanReadableTypes[value]} type={value} />
        </MenuButton>
        <MenuList>
          {[TagType.POSITION, TagType.LANGUAGE, TagType.TECHNOLOGY].filter(v => v !== value).map(tagType => (
              <MenuItem onClick={() => onSelect(tagType)} key={tagType}>
                <TagWithIcon name={humanReadableTypes[tagType]} type={tagType} />
              </MenuItem>
          ))}
        </MenuList>
      </Menu>
  )
}

export default TagTypeSelect